import React, { Component } from 'react'

export default class We extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="we">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInTop">
                <span className="heading-meta">Work Experiences</span>
                <h2 className="colorlib-heading animate-box">Various work that I have done:</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-globe" />
                      </div>
                      <div className="timeline-label">
                        <h2>CHAIRMAN of YOGYAKOMTEK 2019   <span>August 2019 - October 2019</span></h2>
                        <p>Became Chairman of the Organizing Committee for the Largest Computer Exhibition in Yogyakarta.
                          In a collaboration between Computer Science Undergraduate Students of Gadjah Mada University and APKOM (Computer Trader Association) Yogyakarta.</p>
                        <p>Responsibility:</p>
                        <ul>
                            <li>
                            As chairman of the field officer during the computer exhibition and bazaar.
                            </li>
                            <li>
                            Become a liaison between field officers, computer traders, computer trader associations.
                            </li>
                        </ul>
                        <p>Achievements:</p>
                        <ul>
                            <li>
                            ± 25% more visitors visited the computer exhibition and bazaar compared to the previous year.
                            </li>
                            <li>
                            ± 30% more transactions happened in the computer exhibition and bazaar compared to the previous year.
                            </li>
                        </ul>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-globe" />
                      </div>
                      <div className="timeline-label">
                        <h2>CHAIRMAN of JOINTS 2019   <span>December 2018 - May 2019</span></h2>
                        <p>A large-scale event presented by Computer Science Undergraduate Students of Gadjah Mada University.
                            Jogja Information and Technology Session (JOINTS) invites users of information technology all over Indonesia
                            to be wiser and smarter in the utilization of the information technology currently available.</p>
                        <p>Responsibility: leads the team to carry out the event included Tech Talk and Computer Programming Competitions.</p>
                        <p>Achievements:</p>
                        <ul>
                            <li>
                            Better quality and more participants for the Tech Talk than the previous year.
                            </li>
                            <li>
                            More participants and higher prizes for the Computer Programming Competitions than the previous year.
                            </li>
                        </ul>     
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-globe" />
                      </div>
                      <div className="timeline-label">
                        <h2>VICE CHAIRMAN of YOGYAKOMTEK 2018   <span>July 2018 - September 2018</span></h2>
                        <p>Became Vice Chairman of the Organizing Committee for the Largest Computer Exhibition in Yogyakarta.
                          In a collaboration between Computer Science Undergraduate Students of Gadjah Mada University and APKOM (Computer Trader Association) Yogyakarta.</p>
                          <p>Responsibility:</p>
                        <ul>
                            <li>
                            As vice chairman of the field officer during the computer exhibition and bazaar.
                            </li>
                            <li>
                            Helping the chairman become a liaison between field officers, computer traders, computer trader associations.
                            </li>
                        </ul>
                        <p>Achievement:</p>
                        <ul>
                            <li>
                            Collaborating with Intel, the world's largest semiconductor chip manufacturer by revenue, in one of the computer exhibition’s events.
                            </li>
                            <li>
                            ± 30% more transactions happened in the computer exhibition and bazaar compared to the previous year.
                            </li>
                        </ul>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}